<style>
  @import '../assets/styles/dashboard.css';
</style>

<template>
    <div>
        <v-toolbar app
                :fixed="toolbar.fixed"
                :clipped-left="toolbar.clippedLeft">

            <b-dropdown no-caret id="ddowntoolbar" v-if="!($route.name == 'dsc')">
                <template slot="button-content">
                    <v-btn icon>
                        <v-icon>home</v-icon>
                    </v-btn>
                </template>

                <b-dropdown-item v-if="$route.name == 'dsc.menu' || $route.name.indexOf('dsc.') != -1">
                    <router-link to="/dsc" class="standard-a">DSC System</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="
                    $route.name == 'dsc.cde' || 
                    $route.name == 'dsc.cdp' || 
                    $route.name == 'dsc.interfaces' || 
                    $route.name == 'dsc.cdp.cde' || 
                    $route.name == 'dsc.iarc' ||
                    $route.name == 'dsc.iarc.personal' ||
                    $route.name == 'dsc.iarc.information' ||
                    $route.name == 'dsc.interfaces.cde' ||
                    $route.name == 'dsc.dd.technical' ||
                    $route.name == 'dsc.dd.business' ||
                    $route.name == 'dsc.dd.policy'
                    ">
                    <router-link :to="goToDscMenu" class="standard-a">DSC Landing Page</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="
                    $route.name == 'dsc.edmp.iarc.personal' ||
                    $route.name == 'dsc.edmp.dd.technical' ||
                    $route.name == 'dsc.edmp.dd.business' ||
                    $route.name == 'dsc.edmp.dd.consumption'
                    ">
                    <router-link :to="goToDscEdmpMenu" class="standard-a">DSC Landing Page</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'dsc.cdp.cde'">
                    <router-link :to="goToLevel3" class="standard-a">Critical Downstream Process View</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'dsc.interfaces.cde'">
                    <router-link :to="goToLevel3" class="standard-a">Immediate Interface View</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name.indexOf('rfo.') != -1">
                    <router-link to="/rfo" class="standard-a">RFO View</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'rfo.summary' || $route.name == 'rfo.priority'">
                    <router-link :to="goToRfoMenu" class="standard-a">RFO Landing Page</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'ddo.menu' || $route.name.indexOf('ddo.') != -1">
                    <router-link to="/ddo" class="standard-a">DDO View</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="
                    $route.name == 'ddo.businessterm' ||
                    $route.name == 'ddo.systems' ||
                    $route.name == 'ddo.systems.businessterm' ||
                    $route.name == 'ddo.downstream' ||
                    $route.name == 'ddo.downstream.businessterm'
                    ">
                    <router-link :to="goToDdoMenu" class="standard-a">DDO Landing Page</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'ddo.systems.businessterm'">
                    <router-link :to="goToLevel3Ddo" class="standard-a">List of Business Terms mapped to System</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'ddo.downstream.businessterm'">
                    <router-link :to="goToLevel3Ddo" class="standard-a">Downstream Usage</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="$route.name == 'dpo.menu' || $route.name.indexOf('dpo.') != -1">
                    <router-link to="/dpo" class="standard-a">DPO View</router-link>
                </b-dropdown-item>

                <b-dropdown-item v-if="
                    $route.name == 'dpo.dataelements' ||
                    $route.name == 'dpo.datalineage'
                    ">
                    <router-link :to="goToDpoMenu" class="standard-a">DPO Landing Page</router-link>
                </b-dropdown-item>
            </b-dropdown>

            <v-toolbar-title to="/" class="app-title text-capitalize">
                <router-link to="/" class="toolbar-title">
                    {{ $route.name.indexOf('dsc.') != -1 ? ($route.name.indexOf('dsc.edmp') != -1 ? "ENTERPRISE DATA MGMT PLATFORM" : $route.params.system) : (title ? title : "Data Catalogue") }}
                </router-link>
            </v-toolbar-title>
            
            <v-spacer></v-spacer>

            <v-toolbar-title class="login-name">Hi, {{ account.user.Name }}</v-toolbar-title>

            <b-dropdown right no-caret id="ddowntoolbar">
                <template slot="button-content">
                    <v-btn icon>
                        <v-icon>more_vert</v-icon>
                    </v-btn>
                </template>

                <b-dropdown-item @click="doLogout">Logout</b-dropdown-item>
            </b-dropdown>
        </v-toolbar>
    </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
    props: ["title"],
    data () {
        return {
            drawer: {
                clipped: true,
                fixed: false,
                mini: false,
                open: false,
                permanent: false,
            }, 
            toolbar: {
                fixed: true,
                clippedLeft: true
            },
        }
    },
    computed: {
        ...mapState({
            account: state => state.account,
        }),
        ...mapState('account', ['user']),
        isAdmin () {
            return this.user.Role.split(",").indexOf("Admin") != -1
        },
        isDSC () {
            return this.user.Role.split(",").indexOf("DSC") != -1
        },
        isDPO () {
            return this.user.Role.split(",").indexOf("DPO") != -1
        },
        isDDO () {
            return this.user.Role.split(",").indexOf("DDO") != -1
        },
        isRFO () {
            return this.user.Role.split(",").indexOf("RFO") != -1
        },
        goToDscMenu(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 2).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.system);
            return tmp3;
        },
        goToDscEdmpMenu(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 3).join("/");
            return tmp2;
        },
        goToDdoMenu(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 2).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.subdomain);
            return tmp3;
        },
        goToDpoMenu(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 2).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.dspname);
            return tmp3;
        },
        goToLevel3(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 3).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.system);
            return tmp3;
        },
        goToLevel3Ddo(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 3).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.subdomain);
            return tmp3;
        },
        goToRfoMenu(){
            var tmp = this.$route.path.split("/");
            var tmp2 = tmp.slice(0, 2).join("/");
            var tmp3 = tmp2 + '/' + encodeURIComponent(this.$route.params.type);
            return tmp3;
        },
    },
    methods: {
        ...mapActions('account', ['logout']),
        doLogout() {
            this.logout();
        },
        toggleDrawer () {
            if (this.drawer.permanent) {
                this.drawer.permanent = !this.drawer.permanent
                this.drawer.clipped = true
                this.toolbar.clippedLeft = true
            } else {
                this.drawer.open = !this.drawer.open
            }
        }
    }
}
</script>
